class Api {
  constructor(props) {
    this.token = props.token || null; 
    this.hostName = props.hostName;
  }
  
  fetch(url) {  
    return fetch(url)
    .then(response => response.json())
    .then(response => {
      if(response.status !== 'ok') {
        return Promise.reject(response.error);
      }

      return Promise.resolve(response.response);
    });
  }

  add(model, set) {
    const data = JSON.stringify({model, set});
    const url = `${this.hostName}?cmd=add&data=${data}&token=${this.token}`;
    return this.fetch(url);
  }

  update(model, set, where) {
    const data = JSON.stringify({model, set, where});
    const url = `${this.hostName}?cmd=update&data=${data}&token=${this.token}`;
    return this.fetch(url);
  }

  list(model, where) {
    const data = {model};

    if(!isEmpty(where)) {
      data.where = where;
    }

    const url = `${this.hostName}?cmd=list&data=${JSON.stringify(data)}&token=${this.token}`;
    return this.fetch(url); 
  }

  view(model, id) {
    const data = JSON.stringify({model, id});
    const url = `${this.hostName}?cmd=view&data=${data}&token=${this.token}`;
    return this.fetch(url);
  }

  del(model, id) {
    const data = JSON.stringify({model, id});
    const url = `${this.hostName}?cmd=del&data=${data}&token=${this.token}`;
    return this.fetch(url);
  }

  exportExcel(model, where) {
    const data = JSON.stringify({model, where});
    const url = `${this.hostName}?cmd=excel&data=${data}&token=${this.token}`;
    //return this.fetch(url);
    window.open(url);
    return Promise.resolve(null);
  }
}

function isEmpty(obj) {
    for(var key in obj)
    {
        return false;
    }
    return true;
}

export default Api;

